/*Floating Back-To-Top Button*/
#myBtn {
    font-family: Pacifico;
    position: fixed;
    bottom: 7rem;
    float: left;
    left: 0;
    max-width: 50px;
    width: 100%;
    font-size: 18px;
    background-color: #43e19a;
    padding: .5px;
    border-radius: 4px;
    text-decoration: none;

}
/*On Hover Color Change*/
#myBtn:hover {
    background-color: #43e19a;
}